export default function Header() {
    return (
        <header className="header">
            <h4 className="logo">calc</h4>
            <div className="theme-container">
                <p className="theme-title">THEME</p>
                <div className="toggle-container">
                    <p className="theme-header"><span>1</span> <span>2</span> <span>3</span></p>
                    <input type="range" id="toggle-btn" min={1} max={3} step={1}></input>
                </div>
            </div>
        </header>
    )
}