import React from "react"

export default function Calculator() {
    const [input, setinput] = React.useState("");

    const ops = ["+", "-", "*", "/", "."];

    const handleInput = (e) => {

        let value;

        if(e.target.textContent === 'x')
        {
            value = '*'
        }
        else {
            value = e.target.textContent;
        }

        if((ops.includes(value) && input === "") || 
        (ops.includes(value) && ops.includes(input.slice(-1))))
        {
            return
        } 
        else {
            setinput(input + value);
        }
    }

    const handleResult = () => {
        setinput(eval(input).toString());
    }

    const handleReset = () => {
        setinput("");
    }

    const handleDelete = () => {
        setinput(input.slice(0, -1))
    }

    return (
        <main className="">
            <div type="text" className="input-field">{input || 0}</div>
            <div className="keypad">
                <button onClick={handleInput} className="btn">7</button>
                <button onClick={handleInput} className="btn">8</button>
                <button onClick={handleInput} className="btn">9</button>
                <button onClick={handleDelete} className="btn btn-del">DEL</button>
                <button onClick={handleInput} className="btn">4</button>
                <button onClick={handleInput} className="btn">5</button>
                <button onClick={handleInput} className="btn">6</button>
                <button onClick={handleInput} className="btn">+</button>
                <button onClick={handleInput} className="btn">1</button>
                <button onClick={handleInput} className="btn">2</button>
                <button onClick={handleInput} className="btn">3</button>
                <button onClick={handleInput} className="btn">-</button>
                <button onClick={handleInput} className="btn">.</button>
                <button onClick={handleInput} className="btn">0</button>
                <button onClick={handleInput} className="btn">/</button>
                <button onClick={handleInput} className="btn">x</button>
                <button onClick={handleReset} className="btn btn-reset">RESET</button>
                <button onClick={handleResult} className="btn btn-eql">=</button>
            </div>
        </main>
    )
}